import Signup from "./pages/Signup";
import Calendar from "./pages/Calendar";
/* import Test from './pages/Test'; */
import { Routes, Route, BrowserRouter } from "react-router-dom";
import UserInfo from "./pages/UserInfo";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import MyProfile from "./pages/MyProfile";
import axios from "axios";
import Home from "./pages/Home";
import LabelBottomNavigation from "./components/LabelBottomNavigation";
import AddEvent from "./components/AddEvent";

// import Door from "./pages/Door";
/* import Chatbot from "./components/chatbot/Chatbot"; */
import ChatbotSteps from "./components/chatbot/ChatbotSteps";
import Memo from "./pages/Memo";
import {createTheme,ThemeProvider} from "@mui/material";//mui  

/* css   */
import './dabin.css';
import './index.css'

/* mui css css   */
import { CacheProvider } from "@emotion/react";
import createCache from "@emotion/cache";
const cache = createCache({
  key: "css",
  prepend: true,
}); 

/* mui   */
const theme = createTheme({
  typography:{
    fontFamily:['Spoqa Han Sans Neo', 'sans-serif'].join(','),
    fontSize:16
  }
})





function App() {
  const dispatch = useDispatch(); // store     userDispatch 
  useEffect(() => {
    //      session              .
    axios
      .get("/lifeConcierge/api/session") //   get   
      .then((res) => {
        dispatch({ type: "SESSION", session: res.data });
      }) //    redux      .
      .catch(() => {
        console.log("  ");
      });
  });

  return (
    <div className="App">
      {/* theme    */}
      <ThemeProvider theme={theme}>
    <CacheProvider value={cache}>   
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Home />} />
          {/* timeline */}
          <Route path="/calendar" element={<Calendar />} />
          <Route path="/myProfile" element={<MyProfile />} />
          <Route path="/signup" element={<Signup />} />
          <Route path="/userInfo" element={<UserInfo />} />
          <Route path="/addEvent" element={<AddEvent />} />

          {/* <Route path="/" element={<Door />} /> */}
          <Route path="/ChatbotSteps" element={<ChatbotSteps />} />
          <Route path="/Memo" element={<Memo />} />

          {/*  <Route path="/test" element={<Test/>}/> */}
        </Routes>

        <LabelBottomNavigation></LabelBottomNavigation>
      </BrowserRouter>
  </CacheProvider>   
      </ThemeProvider>

      {/*   */}
    </div>
  );
}

export default App;
