// redux  
// import {useSelector, useDispatch} from "react-redux" <-  useDispatch, useSelector import 
// const dispatch = useDispatch(); <- useDispatch   dispatch .
// dispatch() <- dispatch   action()   module.js    reducer   .
// const  = useSelector((state)=>{return(state.)}) <- action  store   useSelector   .


const initialState = { // state  
  session:"",
  snackbar:{
    snackbarToggle:false,
    explain:"",
    severity:""
  },
  progress:{
    progressToggle:false,
  },
  user: {
    userinfo:[]
  },
  confirm: false,
  isEventAdded: false,
}


const reducer = (state = initialState, action) =>{

  switch (action.type){
    case "SESSION":
      return {...state, session: action.session}; // action type "SESSION" {...state, session: action.session}  =>   useSelector .
    case "SNACKBAR/ON":
      return {...state, snackbar: {...state.snackbar, snackbarToggle: action.snackbar.snackbarToggle, explain: action.snackbar.explain, severity:action.snackbar.severity }};
    case "SNACKBAR/OFF":
      return {...state, snackbar: {...state.snackbar, snackbarToggle: action.snackbar.snackbarToggle}};
    case "PROGRESS":
      return {...state, progress: {...state.progress, progressToggle: action.progress.progressToggle}};
    case "USER/USERINFO":
      return {...state, user: {...state.user, userInfo: action.user.userInfo}};
    case "ISLOGGEDIN":
      return {...state, isLoggedin: action.isLoggedin}
    case "ISLOGGEDOUT":
      return {...state, isLoggedout: action.isLoggedout}
    case "ISEVENTADDED":
      return {...state, isEventAdded: action.isEventAdded}
    default:
      return {...state};
  }
};

export default reducer
