import React, { Component } from "react";
import ChatBot from "react-simple-chatbot";
import ChatbotReview from "./ChatbotReview";
import { ThemeProvider } from "styled-components";

/*     */
import greenihead2 from "../../img/greenihead.png"; 
import greenihead from "../../img/greenee.png";
import iconStar from "../../img/iconStar.png";
import iconUnder from "../../img/iconUnder.png";

/*  */
/* Math.random()  0 ~ 1   .  0  
     Math.random()   length .
 Math.random()    *  length    
Math.floor()    . */
let botAvatarRandom;
let randomfirst;
let randomSecond;
let randomThird;
const avatarArray = [greenihead,greenihead2,iconStar,iconUnder]
const firstArray = ["  ? ",' .  ? ','!  ~~']
const secondArray = ['','','','','~ '," !!",' ',' ',' ']
const thirdArray = ['','','',' !', ' ','','',
'    ...','^^','','^^','^^','!','',
'^^*']
function randomF(){
  randomfirst = firstArray[Math.floor(Math.random() * firstArray.length)]
  return randomfirst
}
function randomS(){
  randomSecond = secondArray[Math.floor(Math.random() * secondArray.length)]
  return randomSecond
}
function randomT(){
  botAvatarRandom = avatarArray[Math.floor(Math.random() * avatarArray.length)]
  randomThird = thirdArray[Math.floor(Math.random() * thirdArray.length)]
  return randomThird
}

/*  */

/*  1000  1 . */
let botDelay = 0;
/*     */
const submitButtonStyle = {};
/*    */
const inputStyle = {};
/*    */
const footerStyle = {
  background:"#efefef", 
/*   background: "#fff", */
};
/*     */
const placeholder = "  !";
/*    MAX  (  )*/
/* const width = {}; */

/*   */
const bubbleOptionStyle = {
  background: "#f39c12",
};
/* height    (   ) */
/* const height = {}; */

/* ( ) */
const floatingStyle = {
/*   background: "#efefef", */
  marginRight: '142px',  
  marginBottop: "0px",   
  border: "5px solid",
  borderColor:'#7e7e7e',
  padding: "0px",
  zIndex:'999999999999999999999999',
};

/*  1, 2  */
const lightTheme = {
/*   background: "#fff", */
  background: "#f0fddd",  
  fontFamily: "Helvetica Neue",
  headerBgColor: "#2ecc71",
  headerFontColor: "#fff",
  headerFontSize: "20px",
  botBubbleColor: "#2ecc71",
  botFontColor: "#fff",
  userBubbleColor: "#f39c12",
  userFontColor: "#fff",
};
const darkTheme = {
  background: "#f0fddd",
  fontFamily: "Helvetica Neue",
  headerBgColor: "#2ecc71",
  headerFontColor: "#fff",
  headerFontSize: "20px",
  botBubbleColor: "#2ecc71",
  botFontColor: "#fff",
  userBubbleColor: "#f39c12",
  userFontColor: "#fff",
};

/*  */
const steps = [
  {
    id: "0",
    message: randomF() + randomT(),
    trigger: "user",
  },
  {
    id: "user",
    user:true,
    trigger: "1",
  },
  {
    id: "1",
    message: randomS() + " ?",
    trigger: "transport",
  },
  {
    id: "transport",
    options: [
      { value: "", label: "", trigger: "2" },
      { value: "", label: "", trigger: "2" },
    ],
  },

  {
    id: "2",
    message: randomS() + "? " + randomT(),
    trigger: "job",
  },
  {
    id: "job",
    options: [
      { value: "", label: "", trigger: "3" },
      { value: "", label: "", trigger: "3" },
      { value: "", label: "", trigger: "3" },
      { value: " ", label: "", trigger: "3" },
      { value: "", label: "", trigger: "3" },
      { value: "", label: "", trigger: "3" },
      { value: "", label: "", trigger: "3" },
      { value: "", label: "", trigger: "3" },
      { value: "", label: "", trigger: "3" },
      { value: "", label: "", trigger: "3" },
    ],
  },
  {
    id: "3",
    message: randomS() + " ! " + randomT(),
    trigger: "hobby",
  },
  {
    id: "hobby",
    options: [
      { value: "", label: "", trigger: "4" },
      { value: "", label: "", trigger: "4" },
      { value: "", label: "", trigger: "4" },
      { value: "", label: "", trigger: "4" },
      { value: "", label: "", trigger: "4" },
      { value: "", label: "", trigger: "4" },
      { value: " ", label: "", trigger: "4" },
      { value: "", label: "", trigger: "4" },
      { value: "", label: "", trigger: "4" },
    ],
  },
  {
    id: "4",
    message: randomS() + "  ~ " + randomT(),
    trigger: "food",
  },
  {
    id: "food",
    options: [
      { value: "", label: "", trigger: "5" },
      { value: "", label: "", trigger: "5" },
      { value: "", label: "", trigger: "5" },
      { value: "", label: "", trigger: "5" },
      { value: "", label: "", trigger: "5" },
    ],
  },
  {
    id: "5",
    message: randomS() + " ? " + randomT(),
    trigger: "music",
  },
  {
    id: "music",
    options: [
      { value: "", label: "", trigger: "6" },
      { value: "", label: "", trigger: "6" },
      { value: "", label: "", trigger: "6" },
      { value: "", label: "", trigger: "6" },
      { value: "", label: "", trigger: "6" },
      { value: "", label: "", trigger: "6" },
      { value: "", label: "", trigger: "6" },
    ],
  },
  {
    id: "6",
    message: randomS() + " ? " + randomT(),
    trigger: "review",
  },
  {
    id: "review",
    component: <ChatbotReview />,
    asMessage: true,
    trigger: "update",
  },
  {
    id: "update",
    options: [
      { value: "no", label: "", trigger: "update-yes" },
      /* { value: 'yes', label: '', trigger: 'end-message' }, */
    ],
  },
  {
    id: "update-yes",
    message: "  ?",
    trigger: "update-fields",
  },
  {
    id: "update-fields",
    options: [
      { value: "", label: "", trigger: "update-transport" },
      { value: "", label: "", trigger: "update-job" },
      { value: "", label: "", trigger: "update-hobby" },
      { value: "", label: "", trigger: "update-food" },
      { value: "", label: "", trigger: "update-music" },
    ],
  },
  {
    id: "update-transport",
    update: "transport",
    trigger: "6",
  },
  {
    id: "update-job",
    update: "job",
    trigger: "6",
  },
  {
    id: "update-hobby",
    update: "hobby",
    trigger: "6",
  },
  {
    id: "update-food",
    update: "food",
    trigger: "6",
  },
  {
    id: "update-music",
    update: "music",
    trigger: "6",
  },
/*   {
    id: "end-message",
    message: " .",
    end: true,
  }, */
  /*     {
        id: 'age',
        user: true,
        trigger: '7',
        validator: (value) => {
            if (isNaN(value)) {
                return 'value must be a number';
            } else if (value < 0) {
                return 'value must be positive';
            } else if (value > 120) {
                return `${value}? Come on!`;
            }
            return true;
        },
    }, */
];
/* export */
class ChatbotSteps extends Component {
  render() {
    return (
      <>
        <ThemeProvider theme={lightTheme}>
          {/* <ThemeProvider theme={darkTheme}> */}
          <div
            style={{
              marginRight: "-1px",
              marginTop: "-7.73px",
              /* borderBottom: "65px solid", */
              borderColor: "#fff",
              /* alignItems: "baseline", */
              /* display:'inline', */ 
            }}
          >
            <ChatBot
              headerTitle="Greenee.AI" // 
              steps={steps} //
              botAvatar={botAvatarRandom}
              className="greeniChatbot" //(CSS)()
              hideUserAvatar="true"
              submitButtonStyle={submitButtonStyle}
         /*      width={width} */  /*   MAX   */
              placeholder={placeholder}
              inputStyle={inputStyle}
              footerStyle={footerStyle}
              botDelay={botDelay}
              bubbleOptionStyle={bubbleOptionStyle}
              /* height={height} */
              floating="true" /*     */
              floatingStyle={floatingStyle}
              /* floatingIcon={greenihead}     */
            />
          </div>
        </ThemeProvider>
      </>
    );
  }
}
export default ChatbotSteps;
