import React, { useState } from "react";
import FullCalendar from "@fullcalendar/react"; // must go before plugins
import dayGridPlugin from "@fullcalendar/daygrid"; // a plugin!
import { Button,Dialog,DialogTitle,DialogActions,DialogContent } from "@mui/material"; //mui Dialog
import { useNavigate } from "react-router-dom";
/* import { List, ListItem, ListItemText, Divider } from "@mui/material";
import interactionPlugin from "@fullcalendar/interaction";
import { NextPlan, Preview } from "@mui/icons-material"; */

const Calendar = () => {
  const nav = useNavigate();
  const [open, setOpen] = useState(false); // 

  const handleClick = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const viewEvent = () => {
    
  };

  return (
    <div>
      <FullCalendar //
        //  locale='ko' // 
        height={"700px"}
        titleformat={{
          day: "narrow",
        }}
        events={[
          { title: "", date: "2022-11-28" },
          { title: "event 1", date: "2022-11-22" },
          { title: "event 1", date: "2022-11-19" },
        ]}
        //**   
        eventDisplay="list-item" // ? list-item, none
        eventColor="red"
        eventClick={handleClick}
        plugins={[dayGridPlugin]}
        initialView="dayGridMonth"
        buttonIcons={
          {
            // ?
          }
        }
        buttonText={{
          //   
          today: "today",
          month: "month",
          week: "week",
          day: "day",
          list: "list",
        }}
        headerToolbar={{
          // , today, prev, next 
          start: "prev",
          center: "title",
          end: "next today", //today   
        }}
        titleFormat={function(date) {
          if (date.date.month < 9) {
            return `${date.date.year}.${"0" + (date.date.month + 1)}`;
          }
          return `${date.date.year} ${date.date.month + 1}`;
        }}
        dayNamesShort={["", "", "", "", "", "", ""]}

        //  weekends={false} // 
      />
      <Dialog onClose={handleClose} open={open}>
        {" "}
        {/* onClick={}    */}
        <DialogTitle> </DialogTitle>
        <DialogContent>   </DialogContent>
        <DialogActions>
          <Button>  </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default Calendar;
